import { Injectable, Optional } from '@angular/core';
import { NotifyService, NotifyOptions } from '@farris/ui-notify';
import { LanguageService } from './languag.service';

/**
 * 
 * @scope FormModule
 * @description
 * successerrorwarninginfo
 */
@Injectable()
class FormNotifyService {

  /**
   * 
   * @farris/uiNotifyService
   */
  constructor(private notifyService: NotifyService,
    @Optional() private languageService: LanguageService
  ) {
    if (!languageService) {
      this.languageService = LanguageService.getInstance();
    }
  }

  /**
   * 
   * @param content 
   */
  public default(content: string) {
    return this.notifyService.default(<NotifyOptions>{
      title: this.languageService.notifyTitle,
      msg: content,
      timeout: 3000
    });
  }

  /**
   * 
   * @param content 
   */
  public info(content: string, options?: { [prop: string]: any }) {
    const notifyOptions: NotifyOptions = {
      title: this.languageService.notifyTitle,
      msg: content,
      timeout: 3000
    } as NotifyOptions;
    if (options && options.hideTitle) {
      delete notifyOptions.title;
    }
    return this.notifyService.info(notifyOptions);
  }

  /**
   * 
   * @param content 
   * @param options?  {shorTitle: false}
   */
  public success(content: string, options?: { [prop: string]: any }) {
    const notifyOptions: NotifyOptions = {
      title: this.languageService.notifyTitle,
      msg: content,
      timeout: 3000
    } as NotifyOptions;
    if (options && options.hideTitle) {
      delete notifyOptions.title;
    }
    this.notifyService.success(notifyOptions);
  }

  /**
   * 
   * @param content 
   * @param options 
   */
  public warning(content: string, options?: { [prop: string]: any }) {
    const notifyOptions: NotifyOptions = {
      title: this.languageService.notifyTitle,
      msg: content,
      timeout: 3000
    } as NotifyOptions;
    if (options && options.hideTitle) {
      delete notifyOptions.title;
    }
    this.notifyService.warning(notifyOptions);
  }

  /**
   * 
   * @param content 
   */
  public error(content: string, options?: { [prop: string]: any }) {
    const notifyOptions: NotifyOptions = {
      title: this.languageService.notifyTitle,
      msg: content,
      timeout: 3000
    } as NotifyOptions;
    if (options && options.hideTitle) {
      delete notifyOptions.title;
    }
    this.notifyService.error(notifyOptions);
  }
}

export { FormNotifyService };
