import { Injectable, Optional } from '@angular/core';
import { Observable, empty, of } from 'rxjs';
import { CommandContext } from '@farris/devkit';
import { FormMessageService } from './form-message.service';
import { FormNotifyService } from './form-notify.service';
import { LanguageService } from './languag.service';

/**
 * 
 */
@Injectable()
class DataCheckService {

  constructor(
    private messageService: FormMessageService,
    private formNotifyService: FormNotifyService,
    @Optional() private languageService: LanguageService,
  ) {
    if (!this.languageService) {
      this.languageService = LanguageService.getInstance();
    }
  }

  /**
   * 
   */
  public checkBeforeEdit(idToEdit: string): any {

    const ifSkipCheck = this.ifSkipCheck(idToEdit);
    if (ifSkipCheck === true) {
      return true;
    }

    if (idToEdit) {
      return true;
    }

    this.formNotifyService.warning(this.languageService.plsSelectEditData, { hideTitle: true });
    return empty();
  }

  /**
   * 
   */
  public checkBeforeView(idToView: string): any {
    const ifSkipCheck = this.ifSkipCheck(idToView);
    if (ifSkipCheck === true) {
      return true;
    }

    if (idToView) {
      return true;
    }

    this.formNotifyService.warning(this.languageService.plsSelectViewData, { hideTitle: true });
    return empty();
  }
  /**
   * 
   * @todo
   */
  public ifSkipCheck(id: string): boolean {
    const context = this['context'] as CommandContext;
    const params = context.command.params;

    // 
    if (params.hasOwnProperty('idToEdit') === false && params.hasOwnProperty('idToView') === false) {
      return true;
    }

    // 
    // @todo: undefined
    if (id === '') {
      return true;
    }

    return false;
  }
}

export { DataCheckService };
