
import { Entity, FrameContext, BindingData } from '@farris/devkit';
import { BefRepository, BefRestService } from '@farris/bef';

/**
 * 
 */
class BaseDataService {

  /**
   * 
   */
  protected frameContext: FrameContext;

  /**
   * 
   */
  protected get repository(): BefRepository<Entity> {
    return this.frameContext.repository as BefRepository<Entity>;
  }

  /**
   * Rest
   */
  protected get restService(): BefRestService {
    return this.repository.restService;
  }

  /**
   * 
   */
  protected get bindingData(): BindingData {
    return this.frameContext.bindingData;
  }

  /**
   * 
   */
  constructor(frameContext: FrameContext) {
    this.frameContext = frameContext;
  }

}

export { BaseDataService };
