import { Injectable } from '@angular/core';
import { FrameContext, BindingData } from '@farris/devkit';

@Injectable()
class EntityManipulationService {

  private get bindingData(): BindingData {
    return this.frameContext.bindingData;
  }

  constructor(private frameContext: FrameContext) {
  }

  /**
   * path
   */
  public setPropValue(path: string[], value: any): any {

    // BindingData
    this.bindingData.setValue(path, value, true, true);
  }

}

export { EntityManipulationService };
