import { Injectable, Injector } from "@angular/core";
import { BefRepository, ResponseInfo } from "@farris/bef";
import { Repository } from '@farris/devkit';
import { Observable } from "rxjs";
import { map } from "rxjs/operators";
import { Prop } from "./types";

@Injectable()
export class FeatureRepository {
  constructor(
    private injector: Injector,
    private repository: Repository<any>,
  ) { }
  /**
   * id
   * @param materialId id
   */
  public getFeaturesByMaterialId(materialId: string): Observable<ResponseInfo> {
    const url = '/api/scm/scmfnd/v1.0/scmcommoncmpservice/service/getmaterialprops';
    const body = {
      materialID: materialId
    };
    const options = {
      body
    };
    const befRepository: BefRepository<any> = this.repository as BefRepository<any>;
    return befRepository.proxy.request(url, 'put', null, options);
  }
  /**
   * idhash
   * @param materialId Id
   * @param configID hash
   * @returns 
   */
  public getConfigedValueByFeatureId(materialId: string, configID: string, configType: string = 'Materials'): Observable<ResponseInfo> {
    const url = '/api/bf/df/v1.0/charactconfigservice/service/getcharactconfiginfo';
    const body = { objType: configType, objID: materialId, configID };
    const befRepository: BefRepository<any> = this.repository as BefRepository<any>;
    const options = {
      body
    };
    return befRepository.proxy.request(url, 'put', null, options);
  }
  /**
   * 
   * @param materialId id
   * @param charactValue 
   * @param configType 
   * @returns 
   */
  public applyFeatures(materialId: string, charactValue: any, configType: string = 'Materials') {
    const body = { objType: configType, objID: materialId, charactValue };
    const befRepository: BefRepository<any> = this.repository as BefRepository<any>;
    const options = {
      body
    };
    return befRepository.proxy.request('/api/bf/df/v1.0/charactconfigservice/service/matchcharactconfigid', 'put', null, options);
  }
  /**
   * 
   * @param helpid id
   * @param filter 
   * @param params 
   * @returns 
   */
  public getHelpInfo(helpid: string, filter: string, params: any): Observable<any> {
    const url = '/api/scm/scmfnd/v1.0/scmcommoncmpservice/service/gethelpdata';
    const httpParams: any = {};
    if (params) {
      if (params.pageIndex) {
        httpParams['pageIndex'] = JSON.stringify(params.pageIndex - 0);
      }
      if (params.pageSize) {
        httpParams['pageSize'] = JSON.stringify(params.pageSize - 0);
      }
      if (params.condition) {
        httpParams['condition'] = JSON.stringify(params.condition);
      }
      if (params.searchValue) {
        httpParams['search'] = params.searchValue;
      }
    }
    const body = {
      helpID: helpid,
      queryParam: JSON.stringify(httpParams),
      filterStr: filter
    };
    const befRepository: BefRepository<any> = this.repository as BefRepository<any>;
    const options = {
      body
    };
    return befRepository.proxy.request(url, 'put', null, options);
  }
}