import { ApplicationRef, ChangeDetectorRef, Injectable, Injector } from "@angular/core";

/**
 * angular 
 */
@Injectable()
export class DirtyCheckingService {
  constructor(public injector: Injector, private cd: ChangeDetectorRef, private appRef: ApplicationRef) {
  }
  /**
   * Checks this view and its children. 
   * @param global 
   */
  public detectChanges(global?: boolean | string) {
    global = (global === true || global === 'true') ? true : false;
    if (global && this.appRef) {
      this.appRef.tick();
    } else if (this.cd) {
      this.cd.detectChanges();
    }
  }
  /**
   * msAngular
   * @param duration (ms)
   * @param global 
   */
  public detectChangesAfter(duration: number, global?: boolean | string) {
    global = (global === true || global === 'true') ? true : false;
    if (isNaN(duration)) {
      return;
    }
    if (global && this.appRef) {
      window.setTimeout(() => {
        this.appRef.tick();
      }, duration);
    } else if (this.cd) {
      window.setTimeout(() => {
        this.cd.detectChanges();
      }, duration);
    }
  }
}